import React from 'react';
import { Element } from 'react-scroll';
import ExperianceBox from '../ExperianceBox/ExperianceBox';
import "./ExperianceContainer.css"

const ExperianceContainer = () => {
    return (
        <Element className="experianceContainer" id="exp">
            <h1>Experiance</h1>
            <div className="experianceContainer_info">
                <ExperianceBox number="+10" title="Clients"/>
                <ExperianceBox number="5+" title="Projects" style={{backgroundColor:"#f64c08"}}/>
                <ExperianceBox number="3+" title="Years Teaching"/>
                <ExperianceBox number="500+" title="Students got trained"/>
            </div>

        </Element>
    )
}

export default ExperianceContainer;
